
import wx from 'weixin-js-sdk';
import common from "@/assets/js/common"
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
money: this.$route.query.amount,
isChoose: false,
uid: this.$store.state.uid,
userData: {},
eachWay: [{
text: '',
icon: 'icon-qianbao',
payType: 'yue',
isChoose: false
},
{
text: '',
icon: 'icon-weixin',
payType: 'weixinjs',
isChoose: false
}
]
}
},
components: {
InlineXNumber,
AlertModule,
},
mounted() {
this.getWebUser(this.uid);

this.wxConfig()
},
methods: {
...common,
chooseWay(index) {
var that = this;
for (var i in that.eachWay) {
that.eachWay[i].isChoose = false
}
that.eachWay[index].isChoose = true
},

wxConfig() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var postData = {
url: encodeURI("http://nian.im/app/index.html")
}
that
.$http("get", baseUrl + "WexinJsConfig", postData)
.then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = res.data.data;
wx.config({
debug: false, // ,
appId: result.appId, // corpid
timestamp: result.timestamp, // 
nonceStr: result.nonceStr, // 
jsApiList: ["chooseWXPay"],
signature: result.signature, // 1
jsToken: result.jsToken 
});
}
})
},


submit() {
var that = this;
var baseUrl = that.$store.state.baseUrl;
var index;
var payType=''
for (var i in that.eachWay) {
if (that.eachWay[i].isChoose) {
index = i;
payType=that.eachWay[i].payType
}
}
if(!index){
AlertModule.show({
title: ""
})
}else if (that.money > that.userData.amount && index == 0) {
AlertModule.show({
title: ""
})
} else {
that.$http('post', baseUrl + 'api/Task/Pay/' + that.$route.query.taskId,{
payType:payType
}).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = JSON.parse(res.data.data)
wx.ready(function () {
wx.chooseWXPay({
timestamp: result.timeStamp, // jssdktimestamptimeStampS
nonceStr: result.nonceStr, //  32 
package: result.package, // prepay_idprepay_id=***
signType: result.signType, // 'SHA1''MD5'
paySign: result.paySign, // 
success: function (res) {

that.$router.push({
path: '/paySuccess'
})
},
fail: function (res) {

that.$router.push({
path: '/payFail'
})
}
});
})
}

})
}

}
}
}



