
import AlertModule from 'vux/src/plugins/alert/module'

import wx from 'weixin-js-sdk';
export default {
data() {
return {
moneyList: [{
id: 0,
value: 1,
isChoose: true
}, {
id: 1,
value: 5,
isChoose: false
}, {
id: 2,
value: 10,
isChoose: false
}, {
id: 3,
value: 20,
isChoose: false
}, {
id: 4,
value: 50,
isChoose: false
}, {
id: 5,
value: 99,
isChoose: false
}],
uid:this.$route.query.uid,
amount:1
}
},
components:{
AlertModule
},
mounted(){
this.wxConfig()
},
methods: {
chooseItem(index) {
for (var i in this.moneyList) {
this.moneyList[i].isChoose = false;
}
this.moneyList[index].isChoose = true;
this.amount=this.moneyList[index].value;
},

wxConfig() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var postData = {
url: encodeURI("http://nian.im/app/index.html")
}
that
.$http("get", baseUrl + "WexinJsConfig", postData)
.then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = res.data.data;
wx.config({
debug: false, // ,
appId: result.appId, // corpid
timestamp: result.timestamp, // 
nonceStr: result.nonceStr, // 
jsApiList: ["chooseWXPay"],
signature: result.signature, // 1
jsToken: result.jsToken 
});
}
})
},
giveMoney(){
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http('post', baseUrl + 'api/Task/Red',
{
uid:that.uid,
amount:this.amount,
taskId:this.$route.query.taskId
}).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var  result=JSON.parse(res.data.data)
wx.ready(function () {
wx.chooseWXPay({
timestamp: result.timeStamp, // jssdktimestamptimeStampS
nonceStr: result.nonceStr, //  32 
package: result.package, // prepay_idprepay_id=***
signType: result.signType, // 'SHA1''MD5'
paySign: result.paySign, // 
success: function (res) {

that.$router.push({
path:'/mineIndex'
})
}
});
})
}

})
}
}
}



