
import wx from 'weixin-js-sdk';
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
name: "privateMsg",
components: {
CheckIcon,
AlertModule
},
data() {
return {
yuan1: true,
toAuthod: false,
hongBaoList: [{
yuan: 1,
isChecked: true
},
{
yuan: 5,
isChecked: false
},
{
yuan: 10,
isChecked: false
},
{
yuan: 20,
isChecked: false
},
{
yuan: 50,
isChecked: false
},
{
yuan: 99,
isChecked: false
}
],
content: "",

fromUid: this.$store.state.uid,

uid: this.$route.query.writerId
};
},
mounted() {
this.wxConfig();
this.getUser(this.uid)
},
methods: {
chooseItem(index) {
for (var i in this.hongBaoList) {
this.hongBaoList[i].isChecked = false;
}
this.hongBaoList[index].isChecked = true;
},
getUser(uid) {
var that = this;
var baseUrl = that.$store.state.baseUrl;
that.$http('get', baseUrl + 'api/WebUser/' + uid).then(function (res) {
var type = res.data.data.type;

if (type == 'business') {
that.toAuthod = false
} else {
that.toAuthod = true;

}
})

},

wxConfig() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var postData = {
url: encodeURI("http://nian.im/app/index.html")
}
that
.$http("get", baseUrl + "WexinJsConfig", postData)
.then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = res.data.data;
wx.config({
debug: false, // ,
appId: result.appId, // corpid
timestamp: result.timestamp, // 
nonceStr: result.nonceStr, // 
jsApiList: ["chooseWXPay"],
signature: result.signature, // 1
jsToken: result.jsToken 
});
}
})
},

sendPrivateMsg() {
var that = this;
var baseUrl = this.$store.state.baseUrl;

var postData = {
content: that.content,
uid: that.fromUid,
toUid: that.uid,
amount: 0
};
that.$http("post", baseUrl + "api/PrivateMsg", postData).then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
AlertModule.show({
title: "",
onHide() {
that.$router.push({
path: '/writerDetail',
query: {
writerId: that.uid
},
meta: {
keepAlive: true
}
})
}
});

}
});

},

sendMsgAndMoney() {
var that = this;

var yuan;
for (var i in this.hongBaoList) {
if (this.hongBaoList[i].isChecked) {
yuan = this.hongBaoList[i].yuan;
}
}
var baseUrl = this.$store.state.baseUrl;

var postData = {
content: that.content,
uid: that.fromUid,
toUid: that.uid,
amount: yuan
};
that.$http("post", baseUrl + "api/PrivateMsg", postData).then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
var result = JSON.parse(res.data.data)
wx.ready(function () {
wx.chooseWXPay({
timestamp: result.timeStamp, // jssdktimestamptimeStampS
nonceStr: result.nonceStr, //  32 
package: result.package, // prepay_idprepay_id=***
signType: result.signType, // 'SHA1''MD5'
paySign: result.paySign, // 
success: function (res) {

that.$router.push({
path: '/writerDetail',
query: {
writerId: that.uid
}
})
}
});
})

}
});
}
}
};



